// eslint-disable-next-line
import axios from "axios";
import React, { useEffect, useState } from "react";
import Notiflix from "notiflix";
const ExercisesList = () => {
  const [exercises, setExercises] = useState([]);
  useEffect(() => {
    axios
      .get("https://exostracker.onrender.com/exercises/")
      .then((res) => {
        console.log(res.data);
        setExercises(res.data);
      })
      .catch((err) => console.log(err));
  }, []);

  const handleDelete = (id) => {
    const confirmDelete = window.confirm(
      "Are you sure you want to delete this"
    );
    if (confirmDelete) {
      axios
        .delete(`https://exostracker.onrender.com/exercises/${id}`)
        .then((res) => {
          console.log("Exercises deleted");
          Notiflix.Notify.success("Exercise successfully deleted!");
          setExercises(exercises.filter((exercise) => exercise._id !== id));
        });
    }
  };
  const handleEdit = (id) => {
    window.location = `/edit/${id}`;
  };

  return (
    <div className="container">
      <h1>Exercises List</h1>
      <div className="row">
        {exercises.map((exercise) => (
          <div key={exercise._id} className="col-lg-3 col-md-6 mb-3">
            <div className="card">
              <div className="card-body">
                <h5
                  className="card-title"
                  style={{ textTransform: "capitalize" }}
                >
                  {exercise.description}
                </h5>
                <p className="card-text">
                  Duration: {exercise.duration} minutes
                </p>
                <p className="card-text">Date: {exercise.date}</p>
                <p
                  className="card-text"
                  style={{ textTransform: "capitalize" }}
                >
                  Username: <strong>{exercise.username}</strong>
                </p>
                <button
                  className="btn btn-danger"
                  onClick={() => handleDelete(exercise._id)}
                >
                  Delete
                </button>
                &nbsp; &nbsp;
                <button
                  className="btn btn-info ml-8"
                  onClick={() => handleEdit(exercise._id)}
                >
                  Edit
                </button>
              </div>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default ExercisesList;
